import React from "react";
import AppSearchAPIConnector from "@elastic/search-ui-app-search-connector";

import {
  ErrorBoundary,
  Facet,
  SearchProvider,
  SearchBox,
  Results,
  PagingInfo,
  ResultsPerPage,
  Paging,
  Sorting,
  WithSearch
} from "@elastic/react-search-ui";
import { Layout } from "@elastic/react-search-ui-views";
import "@elastic/react-search-ui-views/lib/styles/styles.css";
import ResultView from "./ResultView";
import {
  buildAutocompleteQueryConfig,
  getConfig,
} from "./config/config-helper";
import moment from "moment";

function ClearFilters({ filters, clearFilters }) {
  return (
    <div>
      <button onClick={() => clearFilters()}>
        Clear {filters.length} Filters
      </button>
    </div>
  );
}


const { hostIdentifier, searchKey, endpointBase, engineName } = getConfig();
const connector = new AppSearchAPIConnector({
  searchKey,
  engineName,
  hostIdentifier,
  endpointBase
});
const config = {
  searchQuery: {
    facets: {
      location: {
      center: "-2.474254, -34.278222",
      type: "range",
      unit: "mi",
      ranges: [
        { from: 0, to: 100, name: "Within 100 miles" },
        { from: 100, to: 500, name: "From 100 to 500 mi" },
        { from: 500, name: "Further than 500 mi" }
      ]
      },
      listingtype: {
        type: "value",
        size: 100
      },
      providerverified: {
        type: "value",
        size: 100
      },
      tagsandcategory: {
        type: "value",
        size: 100
      },
      expiredate: {
        type: "range",
        ranges: [
         {from: moment(), to: moment().add(1, "months").toISOString(), name: "Expires within the Month"},
         {from: moment().add(1, "months").toISOString(), to: moment().add(6,"months").toISOString(), name: "Expires in 2 to 6 Months"},
         {from: moment().add(6, "months"), name: "Expires in over 6 Months"}
        ]
      } 


    }

  },
  autocompleteQuery: buildAutocompleteQueryConfig(),
  apiConnector: connector,
  alwaysSearchOnInitialLoad: true
};



export default function App() {
  return (
    <SearchProvider config={config}>
      <WithSearch mapContextToProps={({ wasSearched, filters, clearFilters }) => ({ wasSearched, filters, clearFilters})}>
        {({ wasSearched, filters, clearFilters }) => {
          return (
            <div className="App">
              <ErrorBoundary>
                <Layout
                  header={<SearchBox autocompleteSuggestions={true} />}
                  sideContent={
                    <div>
                      {wasSearched && (
                        <Sorting
                          label={"Sort by"}
                          sortOptions={[   {
                            name: "Relevance",
                            value: "",
                            direction: ""
                          },
                          {
                            name: "Newest First",
                            value: "activedate",
                            direction: "desc"
                          }]}
                        />
                      )}
                      
                      <Facet field="location" label="Distance" />
                      <Facet field="tagsandcategory" label="Tags"/>
                      <Facet field="listingtype" label="Listing Type" />
                      <Facet field="providerverified" label="Provider Verified?" />
                      <Facet field="expiredate" label="Deal Expiration"/>
                      <ClearFilters filters={filters} clearFilters={clearFilters}/>
                        
                    </div>
                  }
                  bodyContent={
                  <Results
                    resultView={ResultView}
                    titleField={getConfig().titleField}
                    urlField={getConfig().urlField}
                    thumbnailField={getConfig().thumbnailField}
                    shouldTrackClickThrough={true}
                  />
                  }
                  bodyHeader={
                    <React.Fragment>
                      {wasSearched && <PagingInfo />}
                      {wasSearched && <ResultsPerPage />}
                    </React.Fragment>
                  }
                  bodyFooter={<Paging />}
                />
              </ErrorBoundary>
            </div>
          );
        }}
      </WithSearch>
    </SearchProvider>
  );
}
