import React from "react";
import Card from 'react-bootstrap/Card';
const result = ({ result }) => (
  <Card style={{ width: '18rem' }}>
  <Card.Img variant="top" src={result.image.raw} />
  <Card.Body>
    <Card.Title>{ result.title.raw }</Card.Title>
  </Card.Body>
  </Card>
  
);
export default result;